import React, { useState } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox'
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import qr_scan from '../../assets/qr_scan.svg'
import crossIcon from '../../assets/svg/cross-icon.svg'
import warning_amber from '../../assets/svg/warning_amber.svg'
import { COLORS } from "../../helpers/constants";

const StartJob = ({ open, onClose }) => {

  const [errors, setErrors] = useState(new Map());
  const [start, setStart] = useState(false)
  const [data, setData] = useState({})

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const USER_SCHEMA = [
    {
      title: "Name",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.RADIO,
      name: "name",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{ "value": "yes", "label": "Yes" }, { "value": "no", "label": "No" }],
    },
  ]

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="50vw"
      top="20vh"
      wrapperClasses="px-5"
      hideCloseIcon={crossIcon}
    >
      <Stack sx={{ justifyContent: 'center', alignItems: 'center' }}>
        <img
          src={warning_amber}
          onClick={onClose}
          style={{ width: "100px", height: "100px" }}
          alt="warning Icon"
        />
      </Stack>

      <Typography className="fw-24" sx={{ pl: 4 }}>Issue reported on this machine.<br></br>
        Machine not working since
      </Typography>

      <Stack sx={{ justifyContent: 'space-between', flexDirection: 'row', alignItems: 'center', mt: 2, px: 10, pb: 4 }}>
        <Typography className="fw-36" sx={{ color: COLORS.LG_RED }}>02 hrs</Typography>
        <Typography className="fw-36" sx={{ color: COLORS.LG_RED }}>30 mins</Typography>
        <Typography className="fw-36" sx={{ color: COLORS.LG_RED }}>02 sec</Typography>
      </Stack>
      <Stack sx={{ px: 4 }}>
        <FormGroup sx={{ flexDirection: 'row', justifyContent: 'space-between' }}>
          <Typography>Barcode Missing</Typography>
          <FormControlLabel
            control={<Checkbox defaultChecked />}
          />
        </FormGroup>
        <FormGroup sx={{ flexDirection: 'row', justifyContent: 'space-between' }}>
          <Typography>Material not available</Typography>
          <FormControlLabel
            control={<Checkbox defaultChecked />}
          />
        </FormGroup>
        <FormGroup sx={{ flexDirection: 'row', justifyContent: 'space-between' }}>
          <Typography>Barcode available but material missing or less qty</Typography>
          <FormControlLabel
            control={<Checkbox defaultChecked />}
          />
        </FormGroup>
        <FormGroup sx={{ flexDirection: 'row', justifyContent: 'space-between' }}>
          <Typography>Other</Typography>
          <FormControlLabel
            control={<Checkbox defaultChecked />}
          />
        </FormGroup>
      </Stack>
    </Modal>
  );
};

export default StartJob;
