import React, { useEffect, useState, useRef } from "react";
import { useReactToPrint } from 'react-to-print';
import { useLocation, useNavigate } from "react-router-dom";
// import Table from "../../components/table";
import PieChart from '../pie'
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import useUser from "../../store/users";
import useMatrix from "../../store/skillmatrix";
import useSkill from "../../store/skill";
import MTable from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Input from "../../components/input";
import { SCHEMA_TYPES } from "../../helpers/constants";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import PrintIcon from '@mui/icons-material/Print';
import Tooltip from '@mui/material/Tooltip';
import PrintPage from "./index1";

const SkillMatrix = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const companyId = location.state
  const [modalOpen, setModalOpen] = useState("testDetail");
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    tranning_type: '',
    metadata: {},
    skill: ''
  });
  const { list, question_list, test_list, assignExam, createTest, update, remove, addQuestion } = useMatrix();
  const { list: userList } = useUser();
  const { list: skill_list } = useSkill()
  const [errors, setErrors] = useState(new Map());
  const [skills, setSkills] = useState([])
  const [matrixList, setMatrixList] = useState([])
  const [matrix_List, setMatrix_List] = useState([])
  const [printIndex, setPrintIndex] = useState({})
  const [printState, setPrintState] = useState(false)
  const [skillList, setSkillList] = useState([{
    value: 'All',
    key: '123'
  }])

  const form2 = [{
    // title: "Filter By Skill",
    placeholder: 'Filter By Skill',
    type: SCHEMA_TYPES.SELECT,
    name: "skill_id",
    keyValues: skillList
  }]


  useEffect(() => {
    fetchSkill()
    fetchTestList()
    //  fetchUsers()
  }, [])


  const fetchTestList = async () => {
    const [_list, _count] = await userList();
    const newOptr = _list.filter(item => item.is_operator);
    const operatorProfiles = await Promise.all(
      newOptr.map(item => list(page + 1, item.id))
    );
    skills.map((item) => {
      operatorProfiles.map((item2) => {
        console.log("newOptr----item2", item2)
        if (item.id === item2.id) {
          console.log("match skill", item.id)
        }
      })
    })
    setMatrixList(prevOperators => [...prevOperators, ...operatorProfiles]);
    setMatrix_List(prevOperators => [...prevOperators, ...operatorProfiles]);
    setTimeout(() => {
      console.log("newOptr----operatorProfiles", operatorProfiles)
    }, 5000);
  };

  const fetchSkill = async () => {
    const [listss] = await skill_list(page + 1);
    console.log("fetchSkill-----------", listss.results)
    // setFunction(listss.results)
    const newUsers = listss.results.map(item => ({
      value: item.skill_name,
      key: item.id
    }));
    setSkillList(prevUsers => [...prevUsers, ...newUsers]);
    setSkills(listss.results);
  };

  const handleChange = (e) => {
    let tempList = matrixList
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
    if (value === 123) {
      setMatrix_List(tempList)
    }
    else {
      let tmpList = tempList.filter((item) => item[value] > 0)
      setMatrix_List(tmpList.length > 0 ? tmpList : [])
      console.log("name----", name, "value-----", value, "templist", tmpList)
    }

  };

  const handleByAadhar = (e) => {
    const aadharValue = e.target.value;
    // Use findByAadhar to get the matching record
    const findList = findByAadhar(aadharValue);
    console.log("findList----", findList);
    // If findList is null or undefined, set an empty array, otherwise set the found list
    setMatrix_List(findList || []);
  };

  const findByAadhar = (aadhar) => {
    // Use filter instead of find to return an array of matching records (if more than one match is possible)
    return matrixList.filter(record => record.aadhar.startsWith(aadhar));
  };

  const printRef = useRef(null)
  const handlePrintqa = useReactToPrint({
    content: () => printRef.current,
    onPrintError: () => console.log("error------"),
    removeAfterPrint: true,
  });

  function handlePrintq(val) {
    setPrintIndex(val)
    setPrintState(true)
    setTimeout(() => {
      handlePrintqa()
    }, 1000);
  }

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Stack sx={{ flexDirection: 'row', alignItems: 'center' }}>
          {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
          <h1 className="mt-4">
            Skill Matrix
            {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
          </h1>
        </Stack>
      </div>
      <Stack sx={{ flexDirection: 'row', gap: 2 }}>
        <Stack sx={{ width: 120 }}>
          <PieChart
            chartColors={['#AA4A44', COLORS.GRAY, COLORS.GRAY, COLORS.GRAY]}
            chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
          />
          <Typography sx={{ fontSize: 12 }} textAlign='center'>Induction</Typography>
        </Stack>
        <Stack sx={{ width: 120 }}>
          <PieChart
            chartColors={['#AA4A44', '#008FFB', COLORS.GRAY, COLORS.GRAY]}
            chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
          />
          <Typography sx={{ fontSize: 12 }} textAlign='center'>Level 1</Typography>
        </Stack>
        <Stack sx={{ width: 120 }}>
          <PieChart
            chartColors={['#AA4A44', '#008FFB', '#00E396', COLORS.GRAY]}
            chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
          />
          <Typography sx={{ fontSize: 12 }} textAlign='center'>Level 2</Typography>
        </Stack>
        <Stack sx={{ width: 120 }}>
          <PieChart
            chartColors={['#AA4A44', '#008FFB', '#00E396', '#775DD0']}
            chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
          />
          <Typography sx={{ fontSize: 12 }} textAlign='center'>Level 3</Typography>
        </Stack>
      </Stack>
      <Stack sx={{ py: 1, flexDirection: 'row', gap: 1 }}>
        <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={form2}
        />
        <Input placeholder={"Search By Aadhar Number"} onChange={handleByAadhar} />
      </Stack>
      <TableContainer>
        <MTable
        // sx={{ minWidth: minWidth }}
        // className={`${tableClasses}`}
        // size={dense && "small"}
        >
          <TableHead
            sx={{ backgroundColor: COLORS.BG_GRAY }}
          >
            <TableRow>
              <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
                S No.
              </TableCell>
              <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
                Aadhar
              </TableCell>
              {skills.map((item) => {
                return (<TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black', pl: 5 }}>
                  {item.skill_name}
                </TableCell>
                )
              })}
              <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
                Action
              </TableCell>
            </TableRow>
          </TableHead>
          <Stack sx={{ p: 0.2 }}></Stack>
          <TableBody>
            {
              matrix_List.map((item, index) => (
                <TableBodyRow row={item} arr={skills} index={index} childFun={handlePrintq} />
              ))
            }
          </TableBody>
        </MTable>
      </TableContainer>
     {printState===true &&(
       <div style={{visibility:'hidden'}}>
        <div ref={printRef}>
          <PrintPage  skill={skills} row={printIndex}  />
        </div>
      </div>
      )}
    </div>

  );

  function TableBodyRow({ row, arr, index, childFun }) {
    return (
      <>
        <Stack sx={{ p: 0.3 }}>
        </Stack>
        <TableRow
          key={row._id}
          sx={{ backgroundColor: COLORS.LG_GRAY }}
        >
          <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
            {index + 1}
          </TableCell>
          <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
            {row.aadhar}
          </TableCell>
          {
            arr.map((item, index) => {
              let color_1 = ['#AA4A44', COLORS.GRAY, COLORS.GRAY, COLORS.GRAY]
              let color_2 = ['#AA4A44', '#008FFB', COLORS.GRAY, COLORS.GRAY]
              let color_3 = ['#AA4A44', '#008FFB', '#00E396', COLORS.GRAY]
              let color_4 = ['#AA4A44', '#008FFB', '#00E396', '#775DD0']
              let color_5 = [COLORS.GRAY, COLORS.GRAY, COLORS.GRAY, COLORS.GRAY]
              return (<TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black', p: 0, px: 1 }}>
                <Stack sx={{ width: 120 }}>
                  <PieChart
                    chartColors={row[item.id] === 0 ? color_1 : row[item.id] === 1 ? color_2 : row[item.id] === 2 ? color_3 : row[item.id] === 3 ? color_4 : color_5}
                    chart={{ series: [{ value: 25 }, { value: 25 }, { value: 25 }, { value: 25 }] }}
                  />
                </Stack>
              </TableCell>
              )
            })
          }
          <TableCell className="c-gray fw-700 text-nowrap h3" sx={{ color: 'black' }}>
            <Tooltip title="Print Skill Matrix">
              <IconButton
                sx={{ height: 25, width: 25 }}
                onClick={() => childFun(row)}
              >
                <PrintIcon color="primary" fontSize="small" />
              </IconButton>
            </Tooltip>
          </TableCell>
        </TableRow>
      </>
    )
  }
};

export default SkillMatrix;
