import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useDispatch,useSelector } from "react-redux";
import Table from "../../components/table";
import Button from "../../components/button";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Tooltip from '@mui/material/Tooltip';
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import useUser from "../../store/users";
import useQuestion from "../../store/questionBank";
import { Delete } from "@mui/icons-material";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants';
import {setTestRes} from "../../store/questionBank/testItem";
import { Validate } from "../../helpers/utils";


const TestPortal= () => {
  const location = useLocation()
  const row = useSelector((state) => state)
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState()
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
  });
  const { question_list,skill_list,test_list,assignExam,induction_question, createTest,update, remove ,addQuestion,updateTest} = useQuestion();
  const { list:userList,listByAadhar2} = useUser();
  const [file, setFile] = useState(null);
  const [errors, setErrors] = useState(new Map());
  const [skills, setSkills] = useState([])
  const [testList, setTestList] = useState([])
  // const [testId, setTestId] = useState('')

  const TEST = [
    {
      title: "Operator",
      placeholder: "Select Operator",
      type: SCHEMA_TYPES.MASTER,
      name: "operator",
      validator: VALIDATORS.REQUIRED,
      fetch: [listByAadhar2],
      noSearch:true
    },
    {
      title: "Test",
      placeholder: "Select test",
      type: SCHEMA_TYPES.SELECT,
      name: 'test',
      validator: VALIDATORS.REQUIRED,
      keyValues:testList,
    }
   ];

  useEffect(()=>{
     fetchTestList()
     fetchUsers()
  },[])

  const fetchTestList = async () => {
    const [_list, _count] = await test_list();
    const newList = _list.map(item => ({
      value: `${item.test_name}_${item.skill}_${item.skill_level}`,
      key: item.id
    }));
    setTestList(newList);
  };

  const fetchUsers = async () => {
    const [_list, _count] =  await userList(page + 1)
    const newOptr = _list.filter(item => item.is_operator);
    const newUsers = newOptr.map(item => ({
      value: item.username,
      key: item.id
    }));
    setUsers(prevUsers => [...prevUsers,...newUsers]);
  };

const handleAssign =async()=>{
  const _errors = Validate(TEST, data);
  setErrors(_errors);
  if (_errors.size) return;
  let res = await assignExam(data) 
  console.log("response----",res)
  if(res){
  await startNewTab(res)
  }
  return;
}

async function startNewTab(res){
  localStorage.setItem('draweWidth',0)
  const data = { key1: res.id};
  const params = new URLSearchParams(data).toString();
  const url = `/test?${params}`;
  const newTab = window.open(url, '_blank', 'rel=noopener noreferrer');   
}

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <div className="mx-3 my-2"> 
      <div className="flex items-center justify-between">
      <Stack sx={{flexDirection:'row',alignItems:'center'}}>
      {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
        <h1 className="mt-4">
          Testing Portal
          {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
        </h1>
      </Stack>
      {/* <Stack sx={{flexDirection:'row',gap:1}}>
        <Button  onClick={() => setModalOpen("createTest")}>Create Test</Button>
        <Button bordered onClick={() => setModalOpen("addQues")}>Add test</Button>
        <Button  onClick={() => setModalOpen("addAssign")}>Assign test</Button>
        </Stack> */}
      </div>
      <Grid display={'grid'} gap={1} mb={1} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}>
          <SchemaExtractor
              data={data}
              errors={errors}
              onChange={handleChange}
              schema={TEST}
          />
      </Grid>
          <Stack sx={{alignItems:'flex-end'}}>
          <Button onClick={handleAssign}>Create</Button>
          {/* <Button onClick={()=>console.log("daata----",data)}>Create</Button> */}
          </Stack>
        {/* <Table
          columns={COLUMNSS}
          rows={testList}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          paginator
          serialize
          total={total}
         /> */}
        {/* <AddAssign
         open={modalOpen === "addAssign"}
         onClose={() => {setModalOpen(false)}}
         onSubmit={handleAssign}
         users={users}
         testList={testList}
         data={data}
         setData={setData}
        /> */}
    </div>
  );
};

export default TestPortal
