import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useQuestion from "../../store/questionBank";
import Button from "../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid, Tab, Tabs, } from "@mui/material";
import { Delete } from "@mui/icons-material";
import EditIcon from '@mui/icons-material/Edit';
import { COLORS } from "../../helpers/constants";
import JsonFormatter from "../../components/UI/JsonFormatter";
import UploadCsv from "../store/UploadCsv";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants';
import RadioButtonCheckedIcon from '@mui/icons-material/RadioButtonChecked';


const QuestionBank = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const companyId = location.state
  const [tab, setTab] = useState("List")
  const [users, setUsers] = useState([]);
  const [skillFilter, setSkillFilter] = useState([
    {
      value: 'All',
      key: 'All'
    },
    {
      value: 'Induction',
      key: 'Induction'
    },
    {
      value: 'Training',
      key: 'Training'
    }
  ]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState("testDetail");
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });
  const { question_list, skill_list, create, update, remove } = useQuestion();
  const [file, setFile] = useState(null);
  const [csvData, setCsvData] = useState([])
  const [errors, setErrors] = useState(new Map());
  const [filterQues, setFilterQues] = useState([])
  const [ques, setQues] = useState([])

  useEffect(() => {
    fetchSkill();
  }, [])

  useEffect(() => {
    fetchQuestions()
  }, [tab])

  const fetchQuestions = async () => {
    setLoading("fetch");
    const [_list, _count] = await question_list(page + 1);
    setQues(_list);
    setFilterQues(_list)
    setTotal(_count);
    setLoading(false);
  };

  const fetchSkill = async () => {
    setLoading("fetch");
    const [list] = await skill_list(page + 1);
    const newUsers = list.results.map(item => ({
      value: item.skill_name,
      key: item.id
    }));
    setUsers(prevUsers => [...prevUsers, ...newUsers]);
    const newSkills = list.results.map(item => ({
      value: item.skill_name,
      key: item.skill_name
    }));
    setSkillFilter(pre => [...pre, ...newSkills])
    setLoading(false);
  };

  const handleAdd = async () => {
    const payload = { ...data }
    setLoading("fetch");
    edit === true ? await update(payload) : await create(payload, companyId);
    setData({});
    setModalOpen(false);
    // await fetchCompany();
    setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    // await fetchCompany();
    setLoading(false);
  };

  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    await update(id, payload);
    // await fetchCompany();
    setLoading(false);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === "tranning_type") {
      setData({ ...data, skill: '', level: '' })
    }
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleFilter = (e) => {
    let allQuestion = ques
    const { name, value } = e.target;
    console.log("handle filter----", name, value)
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
    if(value === 'Induction'){
      let tmpList = allQuestion.filter((item) =>item.induction_question === true)
      setFilterQues(tmpList.length > 0 ? tmpList : [])
    }
    else if(value ==='Training'){
      let tmpList = allQuestion.filter((item) =>item.induction_question === false && item.skill===null)
      setFilterQues(tmpList.length > 0 ? tmpList : [])
    }
    else if(value !=='Training' && value !== 'Induction' && value !== 'All' && value !==''){
      let tmpList = allQuestion.filter((item) =>item.skill === value)
      setFilterQues(tmpList.length > 0 ? tmpList : [])
    }
    else if(value ==='All'){
     setFilterQues(ques)
    }
   
  };

  const operatorSchema = [
    {
      title: "Test Type",
      placeholder: "Select",
      type: SCHEMA_TYPES.SELECT,
      name: "tranning_type",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{ value: 'Induction Training', key: "Induction Training" }, { value: "Skill Test", key: "Skill Test" }, { value: "Tranning Test", key: "Tranning Test" }]
    },
    data.tranning_type === 'Skill Test' && (
      {
        title: "Skill",
        placeholder: "Select Skill",
        type: SCHEMA_TYPES.SELECT,
        name: "skill",
        validator: VALIDATORS.REQUIRED,
        keyValues: users

      }
    ),
    data.tranning_type === 'Skill Test' && ({
      title: "Tranning Level",
      placeholder: "Select Test Level",
      type: SCHEMA_TYPES.SELECT,
      name: "level",
      validator: VALIDATORS.REQUIRED,
      keyValues: [{ value: 'Level 1', key: 1 }, { value: "Level 2", key: 2 }, { value: "Level 3", key: 3 }]

    })
  ]

  const skillSelect = [
    {
      // title: "Skill",
      placeholder: "Filter By Skill",
      type: SCHEMA_TYPES.SELECT,
      name: "questionFilter",
      validator: VALIDATORS.REQUIRED,
      keyValues: skillFilter
    }
  ]

  const COLUMNS = [
    {
      id: 1,
      title: "Question",
      render: (rowData) => rowData.question_text,
    },
    {
      id: 2,
      title: "Skill",
      render: (rowData) => rowData.skill,
    },
    {
      id: 3,
      title: "Skill Level",
      render: (rowData) => rowData.skill_level ? 'Level ' + rowData.skill_level : '-',
    },
    {
      id: 33,
      title: "Induction Question",
      render: (rowData) => rowData.induction_question === true ? 'True' : 'False',
    },
    {
      id: 4,
      title: "Compulsory",
      render: (rowData) => rowData.compulsory === true ? 'True' : 'False',
    },
    {
      id: 55,
      title: "Status",
      render: (rowData) =>
        <RadioButtonCheckedIcon color={rowData.status === true ? 'success' : 'error'} />
    },
    // {
    //   id: 6,
    //   title: "Actions",
    //   render: (rowData) => (<>
    //     <Stack flexDirection={'row'} sx={{gap:1}}>
    //     {/* <Button width={"10%"} color={rowData.status?"green":"red"}
    //     onClick={() => handleUpdate(rowData?.id,rowData?.status,"status")}
    //     >
    //       {rowData.status?"Active":"Inactive"}
    //     </Button> */}
    //      <IconButton
    //       sx={{ height: 25, width: 25 }}
    //       onClick={() => {setEdit(true);setModalOpen("add")}}
    //     >
    //     <EditIcon color="primary" fontSize="small" />
    //     </IconButton>
    //   <IconButton
    //       sx={{ height: 25, width: 25 }}
    //       onClick={() => handleDelete(rowData?.id)}
    //     >
    //       <Delete color="error" fontSize="small" />
    //     </IconButton>


    //   </Stack>
    //     </>
    //   ),
    // },
  ];

  async function handleCSV() {
    setLoading("fetch");
    const results = [];
    // console.log("tranning_type",data.tranning_type)
    csvData.slice(1).forEach(async (item) => {
      const comp = item[0]
      const ques = item[1];
      const ans = item[item.length - 1]
      const choicesSet = new Set();
      item.slice(2).forEach(choice => {
        if (choice !== '') {
          choicesSet.add(choice);
        }
      });
      const choices = Array.from(choicesSet);
      choices.pop()
      const payload = {
        compulsory: comp === "TRUE" ? true : false,
        question_text: ques,
        question_options: choices,
        correct_answer: parseInt(ans),
        // skill:data.skill,
        // skill_level:data.level,
        induction_question: data.tranning_type === "Induction Training" ? true : false
      };
      const payload2 = {
        compulsory: comp === "TRUE" ? true : false,
        question_text: ques,
        question_options: choices,
        correct_answer: parseInt(ans),
        skill: data.skill,
        skill_level: data.level,
        induction_question: false
      };
      // console.log('induction_question',payload)
      await create(data.tranning_type === 'Skill Test' ? payload2 : payload);
      // results.push(obj);
    });
    // console.log("All results", results);
    setModalOpen(false)
    setLoading(false);
    toast.success("Question are uploaded in question bank.")
    // await fetchQuestions()
    setTab('List')
    setData({})
  }

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Stack sx={{ flexDirection: 'row', alignItems: 'center' }}>
          {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
          <h1 className="mt-4">
            Question Bank
            {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
          </h1>
        </Stack>
        {/* <Button bordered onClick={() => setModalOpen("csvUser")}>upload test</Button> */}
      </div>
      <Tabs value={tab} onChange={(_e, v) => { setTab(v); setPage(0) }} className="mb-2 fw-500">
        {["List", "Upload"].map((label, i) => (
          <Tab label={label} key={i} className="fw-500" value={label} />
        ))}
      </Tabs>

      {tab === "Upload" && (
        <>
          <SchemaExtractor
            data={data}
            errors={errors}
            onChange={handleChange}
            schema={operatorSchema}
          />
          <Stack sx={{ mt: 2, flexDirection: 'row', justifyContent: 'flex-end' }}>
            <Button bordered onClick={() => setModalOpen("csvUser")}>upload CSV file</Button>
          </Stack>
        </>
      )}
      {tab === 'List' && (
        <>
          <Stack sx={{ flexDirection: 'row', mb: 1 }}>
            <SchemaExtractor
              data={data}
              errors={errors}
              onChange={handleFilter}
              schema={skillSelect}
            />
          </Stack>
          <Table
            columns={COLUMNS}
            rows={filterQues}
            rowsPerPage={10}
            loading={loading === "fetch"}
            titleCellClasses="c-gray fw-700"
            page={page}
            setPage={setPage}
            // paginator
            serialize
            total={total}
          />
        </>
      )}
      <UploadCsv
        tab={'question'}
        onClose={() => setModalOpen(false)}
        open={modalOpen === "csvUser"}
        onSubmit={handleCSV}
        file={file}
        setFile={setFile}
        setCsvData={setCsvData}
      />
    </div>
  );
};

export default QuestionBank;
