import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useCompany from "../../store/company";
import Button from "../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid, Divider, Chip } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import Input from "../../components/input";
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import Confirm from "./Confirm";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'

const ChangeLanguage = () => {
  const navigate = useNavigate()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(0);
  const [modalOpen, setModalOpen] = useState(false);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });
  const { list, create, update, remove } = useCompany();

  const USER_SCHEMA = [
    {
      // title: "Name",
      placeholder: "Select Language",
      type: SCHEMA_TYPES.SELECT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    }

  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <h1 className="mt-4">
          Change Language
        </h1>
      </div>

      <Stack sx={{ justifyContent: 'center', alignItems: 'center' }}>

        <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', width: '90%' }}>
          <Stack sx={{ width: '50%' }}>
            <Typography sx={{ fontWeight: 500, fontSize: 18 }}>Existing </Typography>
          </Stack>
          <Stack sx={{ width: '50%', justifyContent: 'center', alignItems: 'center' }}>
            <Typography sx={{ fontWeight: 500, fontSize: 18 }}>Selected  </Typography>
          </Stack>
        </Stack>
        <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', mt: 1, width: '90%' }}>
          <Typography sx={{ fontWeight: 500, fontSize: 16 }}>English  </Typography>
          <Stack sx={{ width: 420 }}>
            <SchemaExtractor
              data={data}
              errors={errors}
              onChange={handleChange}
              schema={USER_SCHEMA}
            />
          </Stack>
        </Stack>

      </Stack>

      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={6}
        mb={1}
        px={4}
      // sx={{px:6}}
      >
        <Button bordered gray >
          Cancel
        </Button>
        <Button color={'gray'} onClick={() => setModalOpen("confirm")}>
          <Typography color={COLORS.TEXT_GRAY}>
            Confirm
          </Typography>
        </Button>
      </Stack>
      <Confirm
        open={modalOpen === "confirm"}
        onClose={() => { setModalOpen(false) }}
      />


    </div>


  );
};

export default ChangeLanguage;
